.forming {
  margin-top: 5vh;
  max-width: 290px;
}

@media (min-width:1300px) {
  .karcenter {
    position: absolute;
    top: 80px;
    left: 1000px;
  }
}

@media (min-width:1190px) and (max-width:1299px) {
  .karcenter {
    position: absolute;
    top: 120px;
    left: 850px;
  }
}

/* @media (max-width:1189) { */
@media screen and (max-width: 996px) {
    .karcenter {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  z-index: 2;
}
  
}

.reg-box {
  width: 100%;
  max-width: 315px;
  background: #fff;
  color: #132273;
  border-radius: 8px;
  padding: 12px;
  box-shadow: 0 2px 12px rgba(88, 81, 81, 0.07);
  box-sizing: border-box;
}

.reg-title {
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.4;
  margin-bottom: 12px;
}

.reg-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.reg-input,
.reg-select {
  width: 100%;
  border: 1px solid #d3d4d3;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: 12px;
  color: #333;
  background: #fff;
  box-sizing: border-box;
}

.reg-label {
  font-size: 13px;
  font-weight: 500;
  margin-bottom: 4px;
}

.reg-phone-wrapper {
  display: flex;
  align-items: center;
  border: 1px solid #d3d4d3;
  border-radius: 4px;
  padding: 0 6px;
  background: #fff;
}

.reg-phone-prefix {
  margin-right: 6px;
  color: #333;
  font-size: 12px;
  -webkit-user-select: none;
  user-select: none;
}

.reg-checkbox-row {
  display: flex;
  align-items: flex-start;
  gap: 7px;
  margin-top: 4px;
}

.captcha-box {
  display: flex;
  align-items: center;
  gap: 10px;
}

.captcha {
  font-size: 16px;
  font-weight: bold;
  background-color: #f2f2f2;
  padding: 5px 10px;
  letter-spacing: 5px;
  border: 1px dashed #999;
  -webkit-user-select: none;
  user-select: none;
}

.error-message {
  font-size: 12px;
  color: red;
  margin-top: 5px;
  display: none;
}

.reg-checkbox {
  accent-color: #132273;
  margin-top: 1px;
}

.reg-checkbox-label {
  font-size: 12px;
  line-height: 14px;
  color: #132273;
  -webkit-user-select: none;
  user-select: none;
}

.reg-button {
  margin-top: -12px;
  background: #132273;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 11px 0;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  width: 100%;
  transition: opacity 0.18s;
}

.reg-button:hover,
.reg-button:active {
  opacity: 0.82;
}

.reg-footer-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 18px;
  font-size: 13px;
  color: #222;
}

.reg-login-link {
  color: #132273;
  text-decoration: underline;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
}

@media (max-width: 550px) {
  .reg-box {
    /* max-width: 96vw; */
    padding: 12px;
  }
}

@media (max-width:1189) and (min-width:250px) {
  .karcenter {
    max-width: 300px;
  }

  .forming {
    display: flex;
    justify-content: center;
  }
}